.promo {
  &__footer-inner {
    @include adaptiv-value(margin-top, 60, 40, 1);
    @include adaptiv-value(margin-bottom, 70, 20, 1);
  }

  &__footer-title {
    margin-bottom: 45px;
    @include adaptiv-value(font-size, 50, 32, 1);
    font-weight: 550;
    line-height: 110%;
    text-align: center;
    color: #31333e;
  }

  &__footer-blocks {
    display: flex;
    justify-content: space-between;
    gap: 20px;
    @media screen and (width <= 750px) {
      flex-direction: column;
      align-items: center;
    }
  }

  &__footer-block {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    border-radius: 20px;
    padding: 40px 10px 40px 40px;
    width: 100%;
    background-color: white;
    transition: all 0.3s ease;
    cursor: pointer;
    @include adaptiv-value(max-width, 453, 345, 1);
    @media screen and (width <= 450px) {
      padding: 20px 10px 40px 25px;
    }
  }

  //   &__footer-img {
  //   }

  &__footer-img:nth-child(1) {
    width: 110px;
    height: 85px;
  }

  &__footer-img:nth-child(2) {
    width: 65px;
    height: 97px;
  }

  &__footer-img:nth-child(3) {
    width: 80px;
    height: 61px;
  }

  &__footer-block-title {
    @include adaptiv-value(margin-top, 37, 20, 1);
    @include adaptiv-value(margin-bottom, 40, 30, 1);
    transition: all 0.3s ease;
  }

  &__footer-block:hover {
    .promo {
      &__footer-block-title {
        color: #0084ff;
      }
    }
  }

  //   &__head-btn {
  //   }
}
