.title-main {
  @include adaptiv-value-to(font-size, 60, 30, 375);

  // @include adaptiv-value(font-size, 60, 32, 1);
  @include adaptiv-value(margin-top, 55, 34, 1);
  @include adaptiv-value(margin-bottom, 44, 36, 1);
  font-weight: 550;
  line-height: 110%;
  text-align: center;
  color: #31333e;
}

.promo {
  &__header-inner {
    display: flex;
    justify-content: space-between;
    gap: 20px 30px;
    @media screen and (width <= 1050px) {
      flex-direction: column;
      align-items: center;
    }
  }

  &__header-block {
    display: flex;
    justify-content: space-between;
    border-radius: 20px;
    width: 100%;
    max-width: 695px;
    background-color: white;
    cursor: pointer;
    gap: 20px;

    &:nth-child(1) {
      overflow: hidden;
    }
    @media screen and (width <= 600px) {
      flex-direction: column;
      gap: 0;
    }
  }

  &__header-discr {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 32px;
    margin-bottom: 37px;
    margin-left: 40px;
    @media screen and (width <= 600px) {
      margin-bottom: 0;
    }
  }

  &__header-title {
    width: 100%;
    max-width: 280px;
  }

  &__header-title.arrow-right::after {
    display: none;
    width: 7.1px;
    height: 11.5px;
  }

  &__header-title:nth-child(1)::after {
    @media screen and (width <= 600px) {
      right: 0;
      top: 40px;
      display: block;
      background-image: url('./../img/arrow_right_blue.svg');
    }
    @media screen and (width <= 420px) {
      right: 40px;
      top: 33px;
    }
  }

  &__header-block:nth-child(2) {
    .promo__header-title::after {
      @media screen and (width <= 600px) {
        right: -20px;
      }
      @media screen and (width <= 420px) {
        right: 15px;
      }
    }
  }

  &__header-text {
    margin-top: 10px;
    @include adaptiv-value(margin-bottom, 46, 30, 1);
    font-weight: 400;
    font-size: 18px;
    line-height: 140%;
    color: #90929a;
  }

  &__header-img {
    position: relative;
    border-radius: 0 15px 15px 0;
    width: 296px;
    min-height: 100%;
    background: linear-gradient(133.04deg, #98b5ff 9.59%, rgb(153 182 255 / 76%) 94.37%);
    @media screen and (width <= 600px) {
      border-radius: 0 0 15px 15px;
      width: 100%;
      height: 145px;
    }
  }

  &__header-btn {
    @media screen and (width <= 600px) {
      display: none;
    }
  }
}

.promo-car {
  &::before,
  &::after {
    content: '';
    position: absolute;
    bottom: 0;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }

  &::before {
    right: -1px;
    z-index: 2;
    width: 380px;
    height: 243px;
    background-image: url('./../img/car.png');

    @media screen and (width <= 1200px) {
      width: 310px;
      height: 180px;
    }
    @media screen and (width <= 1050px) {
      width: 380px;
      height: 243px;
    }
    @media screen and (width <= 660px) {
      width: 305px;
      height: 220px;
    }
    @media screen and (width <= 600px) {
      @include adaptiv-value-in-min(width, 430, 330, 600, 375);
      @include adaptiv-value-in-min(height, 260, 215, 600, 375);
    }
  }

  &::after {
    right: 0;
    width: 100%;
    height: 100%;
    background-image: url('./../img/percent-car.png');
    @media screen and (width <= 600px) {
      background-image: none;
    }
  }
}

.promo-percent {
  &::before,
  &::after {
    content: '';
    position: absolute;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }

  &::before {
    left: -75px;
    top: -35px;
    z-index: 2;
    width: 435.47px;
    height: 356.73px;
    background-image: url('./../img/arrow_around.svg');
    @media screen and (width <= 600px) {
      left: 0;
      left: 50%;
      top: 50%;
      width: 345px;
      height: 236.67px;
      background-image: url('./../img/arrow_around_m.svg');
      transform: translate(-50%, -50%);
    }
  }

  &::after {
    width: 100%;
    height: 100%;
    background-image: url('./../img/200k.png');
  }

  &-text {
    position: absolute;
    left: 50%;
    top: 50%;
    font-weight: 700;
    @include adaptiv-value(font-size, 90, 75, 1);
    line-height: 130%;
    color: white;
    transform: translate(-50%, -50%);
  }
}
