.chat {
  &__form-inner {
    display: flex;
    column-gap: 30px;
    justify-content: space-between;
    margin-top: 50px;
    border-top: 1px solid #f4f6f7;
    @include adaptiv-value(padding-top, 50, 38, 1);
    @include adaptiv-value(margin-bottom, 70, 15, 1);

    @media screen and (width <= 750px) {
      flex-direction: column;
      align-items: center;
      row-gap: 25px;
    }
  }

  &__form-block {
    flex: 1 1 50%;
    @media screen and (width <= 750px) {
      flex: 1 1 100%;

      &:nth-child(1) {
        max-width: 375px;
      }
    }
  }
}
